.main{
    background-image: -webkit-gradient(linear, left bottom, left top, from(#a8edea), to(#fed6e3));
    background-image: -webkit-linear-gradient(bottom, #a8edea 0%, #fed6e3 100%);
    background-image: -o-linear-gradient(bottom, #a8edea 0%, #fed6e3 100%);
    background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
    height: 100vh;
    width: 100%;    
}

.btn{
    text-transform: uppercase;
    text-decoration: none;
    padding: 15px 40px;
    display: inline-block;
    border-radius: 100px;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
    position: relative;
    font-size: 12px;
    border: none;
    cursor: pointer;
    background-color: red;
}

a.btn{    
    text-decoration:none;
    font-size: 15px;
    background-color: #fff;
    color: #777;
    font-family: 'Roboto', sans-serif;
}

.btn::after {
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 100px;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    transition: all .4s;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    background-color: white;
}


.btn:active,
.btn:focus {
    outline: none;
    -webkit-transform: translateY(-1px);
        -ms-transform: translateY(-1px);
            transform: translateY(-1px);
    -webkit-box-shadow: 0 .5rem 1rem rgba(black, .2);
            box-shadow: 0 .5rem 1rem rgba(black, .2);
}

.btn:hover{
    -webkit-transform: translateY(-3px);
        -ms-transform: translateY(-3px);
            transform: translateY(-3px);
    -webkit-box-shadow: 0 1rem 2rem rgba(white, .2);
            box-shadow: 0 1rem 2rem rgba(white, .2);
}

.btn:hover::after{
    -webkit-transform: scaleX(1.4) scaleY(1.6);
        -ms-transform: scaleX(1.4) scaleY(1.6);
            transform: scaleX(1.4) scaleY(1.6);
    opacity: 0;
}

.btn--white {
    background-color: #fff;
    color: #777;
}

.animation-right{
    -webkit-animation: moveFromRight 1s ease-out .4s;
            animation: moveFromRight 1s ease-out .4s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

.animation-later{
    -webkit-animation: moveFromRight 1s ease-out .4s;
            animation: moveFromRight 1s ease-out .4s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}
.animation{
    -webkit-animation: moveBtnRight 1s ease-out .4s;
            animation: moveBtnRight 1s ease-out .4s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

.animation-long{
    -webkit-animation: moveBtnRight 2s ease-out 1s;
            animation: moveBtnRight 2s ease-out 1s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

@-webkit-keyframes moveBtnRight{
    0% {
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes moveBtnRight{
    0% {
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}


@-webkit-keyframes moveFromRight{
    0% {
        opacity: 0;
        -webkit-transform: translateX(3rem);
                transform: translateX(3rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }

}


@keyframes moveFromRight{
    0% {
        opacity: 0;
        -webkit-transform: translateX(3rem);
                transform: translateX(3rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }

}

.main-container{
    background-color: paleturquoise;
    height: 200px;
    padding-top: 7%;
    width: 900px;
    margin: 0 auto;
}

.title{
    font-size: 40px;
    font-family: 'Courgette', cursive;
    text-align:center;
}

.flex{
    display: grid;
    grid-template-columns: 1fr 1fr;

    
}

.flex1{
    padding-top: 50%;
    text-align: center;
      
}

